import React, { useState } from "react";
import { Map, Marker, Popup, TileLayer } from "react-leaflet";
import { Icon } from "leaflet";
import * as locationData from "../data/locationData.json";
import * as regionalData from "../data/regionalData.json";
import * as nakuruData from "../data/nakuru_regionalData.json";
import * as mombasaData from "../data/mombasa_regionalData.json";
import * as garissaData from "../data/garissa_regionalData.json";
import "../App.css";

const FinalMap = () => {
  const [headquarters, setHeadquarters] = useState(null);
  const [regions, setRegions] = useState(null);
  const [showRegions, setShowRegions] = useState(false);
  const [nakuruFieldOffices, setNakuruFieldOffices] = useState(null);
  const [showNakuruOffices, setShowNakuruOffices] = useState(false);
  const [mombasaFieldOffices, setMombasaFieldOffices] = useState(null);
  const [showMombasaOffices, setShowMombasaOffices] = useState(false);
  const [garissaFieldOffices, setGarissaFieldOffices] = useState(null);
  const [showGarissaOffices, setShowGarissaOffices] = useState(false);

  const regionIcon = new Icon({
    iconUrl:
      "http://chart.apis.google.com/chart?chst=d_map_pin_letter&chld=%E2%80%A2|e85141&chf=a,s,ee00FFFF",
  });

  const fieldIcon = new Icon({
    iconUrl:
      "http://chart.apis.google.com/chart?chst=d_map_pin_letter&chld=%E2%80%A2|2ecc71&chf=a,s,ee00FFFF",
  });

  return (
    // This is where laying out of the mapping is done.
    <Map center={[-0.023559, 37.906193]} zoom={7.3}>
      {/* // Footer of the map */}
      <TileLayer
        url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
        attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors'
      />

      {/* mapping of the headquarter office and it's popup */}
      {locationData.features.map((hq) => (
        <Marker
          key={hq.properties.PARK_ID}
          position={[hq.geometry.coordinates[1], hq.geometry.coordinates[0]]}
          onClick={() => {
            setRegions(regions);
            setShowRegions(true);
          }}
          onmouseover={() => {
            setHeadquarters(hq);
          }}
        />
      ))}

      {headquarters && (
        <Popup
          position={[
            headquarters.geometry.coordinates[1],
            headquarters.geometry.coordinates[0],
          ]}
          onClose={() => {
            setHeadquarters(null);
          }}
        >
          <div>
            <h2>{headquarters.properties.NAME}</h2>
            Number of Staff: <b>{headquarters.properties.STAFF_NUM}</b> <br />
            Issues affecting office: <b>
              {headquarters.properties.ISSUES}
            </b>{" "}
            <br />
            Last Report as at: <b>
              {headquarters.properties.MODIFIED_DATE}
            </b> at <b>{headquarters.properties.LAST_REPORT_TIME}</b> <br />
            Reporting to: <b>{headquarters.properties.REPORTING_TO}</b>
          </div>
        </Popup>
      )}

      {/* mapping of the regional offices and it's popup */}

      {showRegions
        ? regionalData.features.map((region) => (
            <Marker
              key={region.properties.PARK_ID}
              position={[
                region.geometry.coordinates[1],
                region.geometry.coordinates[0],
              ]}
              onClick={() => {
                if (region.properties.NAME === "Nakuru Regional Office") {
                  setNakuruFieldOffices(nakuruFieldOffices);
                  setShowNakuruOffices(true);
                  setShowMombasaOffices(false);
                  setShowGarissaOffices(false);
                } else if (
                  region.properties.NAME === "Mombasa Regional Office"
                ) {
                  setMombasaFieldOffices(mombasaFieldOffices);
                  setShowMombasaOffices(true);
                  setShowNakuruOffices(false);
                  setShowGarissaOffices(false);
                } else {
                  setGarissaFieldOffices(garissaFieldOffices);
                  setShowMombasaOffices(false);
                  setShowNakuruOffices(false);
                  setShowGarissaOffices(true);
                }
              }}
              onmouseover={() => {
                setRegions(region);
              }}
              icon={regionIcon}
            />
          ))
        : ""}

      {regions && (
        <Popup
          position={[
            regions.geometry.coordinates[1],
            regions.geometry.coordinates[0],
          ]}
          onClose={() => {
            setRegions(null);
          }}
        >
          <div>
            <h2>{regions.properties.NAME}</h2>
            Number of Staff: <b>{regions.properties.STAFF_NUM}</b> <br />
            Issues affecting office: <b>{regions.properties.ISSUES}</b> <br />
            Last Report as at: <b>{regions.properties.MODIFIED_DATE}</b> at{" "}
            <b>{regions.properties.LAST_REPORT_TIME}</b> <br />
            Reporting to: <b>{regions.properties.REPORTING_TO}</b>
          </div>
        </Popup>
      )}

      {/* mapping of the nakuru field offices and it's popup */}

      {showNakuruOffices
        ? nakuruData.features.map((nakuru_field_office) => (
            <Marker
              key={nakuru_field_office.properties.PARK_ID}
              position={[
                nakuru_field_office.geometry.coordinates[1],
                nakuru_field_office.geometry.coordinates[0],
              ]}
              onClick={() => {
                setNakuruFieldOffices(nakuruFieldOffices);
              }}
              onmouseover={() => {
                setNakuruFieldOffices(nakuru_field_office);
              }}
              icon={fieldIcon}
            />
          ))
        : ""}

      {nakuruFieldOffices && (
        <Popup
          position={[
            nakuruFieldOffices.geometry.coordinates[1],
            nakuruFieldOffices.geometry.coordinates[0],
          ]}
          onClose={() => {
            setNakuruFieldOffices(null);
          }}
        >
          <div>
            <h2>{nakuruFieldOffices.properties.NAME}</h2>
            Number of Staff: <b>
              {nakuruFieldOffices.properties.STAFF_NUM}
            </b>{" "}
            <br />
            Issues affecting office:{" "}
            <b>{nakuruFieldOffices.properties.ISSUES}</b>
            <br />
            Last Report as at:{" "}
            <b>{nakuruFieldOffices.properties.MODIFIED_DATE}</b> at{" "}
            <b>{nakuruFieldOffices.properties.LAST_REPORT_TIME}</b> <br />
            Reporting to: <b>{nakuruFieldOffices.properties.REPORTING_TO}</b>
          </div>
        </Popup>
      )}

      {/* mapping of the mombasa field offices and it's popup */}

      {showMombasaOffices
        ? mombasaData.features.map((mombasa_field_office) => (
            <Marker
              key={mombasa_field_office.properties.PARK_ID}
              position={[
                mombasa_field_office.geometry.coordinates[1],
                mombasa_field_office.geometry.coordinates[0],
              ]}
              onClick={() => {
                setMombasaFieldOffices(mombasaFieldOffices);
              }}
              onmouseover={() => {
                setMombasaFieldOffices(mombasa_field_office);
              }}
              icon={fieldIcon}
            />
          ))
        : ""}

      {mombasaFieldOffices && (
        <Popup
          position={[
            mombasaFieldOffices.geometry.coordinates[1],
            mombasaFieldOffices.geometry.coordinates[0],
          ]}
          onClose={() => {
            setMombasaFieldOffices(null);
          }}
        >
          <div>
            <h2>{mombasaFieldOffices.properties.NAME}</h2>
            Number of Staff: <b>
              {mombasaFieldOffices.properties.STAFF_NUM}
            </b>{" "}
            <br />
            Issues affecting office:{" "}
            <b>{mombasaFieldOffices.properties.ISSUES}</b>
            <br />
            Last Report as at:{" "}
            <b>{mombasaFieldOffices.properties.MODIFIED_DATE}</b> at{" "}
            <b>{mombasaFieldOffices.properties.LAST_REPORT_TIME}</b> <br />
            Reporting to: <b>{mombasaFieldOffices.properties.REPORTING_TO}</b>
          </div>
        </Popup>
      )}

      {/* mapping of the garissa field offices and it's popup */}

      {showGarissaOffices
        ? garissaData.features.map((garissa_field_office) => (
            <Marker
              key={garissa_field_office.properties.PARK_ID}
              position={[
                garissa_field_office.geometry.coordinates[1],
                garissa_field_office.geometry.coordinates[0],
              ]}
              onClick={() => {
                setGarissaFieldOffices(garissaFieldOffices);
              }}
              onmouseover={() => {
                setGarissaFieldOffices(garissa_field_office);
              }}
              icon={fieldIcon}
            />
          ))
        : ""}

      {garissaFieldOffices && (
        <Popup
          position={[
            garissaFieldOffices.geometry.coordinates[1],
            garissaFieldOffices.geometry.coordinates[0],
          ]}
          onClose={() => {
            setGarissaFieldOffices(null);
          }}
        >
          <div>
            <h2>{garissaFieldOffices.properties.NAME}</h2>
            Number of Staff: <b>
              {garissaFieldOffices.properties.STAFF_NUM}
            </b>{" "}
            <br />
            Issues affecting office:{" "}
            <b>{garissaFieldOffices.properties.ISSUES}</b>
            <br />
            Last Report as at:{" "}
            <b>{garissaFieldOffices.properties.MODIFIED_DATE}</b> at{" "}
            <b>{garissaFieldOffices.properties.LAST_REPORT_TIME}</b> <br />
            Reporting to: <b>{garissaFieldOffices.properties.REPORTING_TO}</b>
          </div>
        </Popup>
      )}
    </Map>
  );
};

export default FinalMap;
